function handlePrev(){
    if(page==sliceIntoChunks(nums, 10)[paginationValue][0]){
        setPaginationValue(paginationValue-1)
    }
    if (window.innerWidth<1000){
        window.scrollTo(0, 500)
    }
    else{
        window.scrollTo(0,0)
    }
    setPage((p)=>{
        if(p===1) return p;
        return p-1;
    })
}
function handleNext(){
    if(page==sliceIntoChunks(nums, 10)[paginationValue][sliceIntoChunks(nums, 10)[paginationValue].length-1]){
        setPaginationValue(paginationValue+1)
    }
    if (window.innerWidth<1000){
        window.scrollTo(0, 500)
    }
    else{
        window.scrollTo(0,0)
    }
    

    setPage((p)=>{
        if(p===pageCount) return p
        return p+1;
    })
}

   




