import React, {useCallback, useContext, useEffect, useState} from 'react'
import {withRouter } from "react-router-dom";
import videojs from 'video.js';
import './Post.css';
import 'video.js/dist/video-js.css';


import axios from 'axios'
import Application from './Application'


const Post=({title,element,videos}) => {
console.log(videos)
console.log(JSON.parse(localStorage.getItem('page'))[JSON.parse(localStorage.getItem('page')).length-1].data.videoId)

const RelatedVideos=videos.filter((video)=>{
        
    return video.data.title.includes(element.data.tags.replace(/\s/g, '').split(',')[0]) || video.data.tags.includes(element.data.tags.replace(/\s/g, '').split(',')[0])
    
})
const RelatedVideos2=RelatedVideos.filter((video)=>{
        
    return video.data.title.includes(element.data.tags.replace(/\s/g, '').split(',')[1]) || video.data.tags.includes(element.data.tags.replace(/\s/g, '').split(',')[1])
    
})
console.log(RelatedVideos2)

    const VideoJS = (props) => {
        const videoRef = React.useRef(null);
        const playerRef = React.useRef(null);
        const {options, onReady} = props;
      
        React.useEffect(() => {
      
          // Make sure Video.js player is only initialized once
          if (!playerRef.current) {
            const videoElement = videoRef.current;
      
            if (!videoElement) return;
      
            const player = playerRef.current = videojs(videoElement, options, () => {
              videojs.log('player is ready');
              onReady && onReady(player);
            });
      
          // You could update an existing player in the `else` block here
          // on prop change, for example:
          } else {
            // const player = playerRef.current;
      
            // player.autoplay(options.autoplay);
            // player.src(options.sources);
          }
        }, [options, videoRef]);
      
        // Dispose the Video.js player when the functional component unmounts
        React.useEffect(() => {
          const player = playerRef.current;
      
          return () => {
            if (player) {
              player.dispose();
              playerRef.current = null;
            }
          };
        }, [playerRef]);
      
        return (
          <div data-vjs-player>
            <video ref={videoRef}   poster={props.poster} className='video-js vjs-16-9 vjs-layout-medium' />
          </div>
        );
      }


    const playerRef = React.useRef(null);

    const videoJsOptions = {
      autoplay: true,
      controls: true,
      responsive: true,
      fluid: true,
      sources: [{
        src:element.data.downloadVideo,
        type: 'video/mp4'
      }]
    };
  
    const handlePlayerReady = (player) => {
      playerRef.current = player;
  
      // You can handle player events here, for example:
      player.on('waiting', () => {
        videojs.log('player is waiting');
      });
  
      player.on('dispose', () => {
        videojs.log('player will dispose');
      });
    };

    return (
      <>
      <div className="All">
        <div className="VideoContainer">
<div className="videoItems">
    <div className="videoTitle"><h1>{element.data.title}</h1></div>
    <div className="videoText">Occurred on {new Date(element.data.date).toLocaleDateString("en-US", { year: 'numeric', month: 'long', day: 'numeric'})}/{element.data.city == '-' ? element.data.country :`${element.data.city}, ${element.data.country}`}
<br />
<br />
{element.data.description}
</div>
<div style={{
  display:"block",
  
    
    height: window.innerWidth<768 && window.innerWidth>500 ? '500px' : "300px"
}} className="videoJsItems">
<div className="videoJs">
<VideoJS  poster={element.data.downloadPhoto} options={videoJsOptions} onReady={handlePlayerReady} />

</div>
{/* <div style={{
  display: window.innerWidth>1000 ? "block" : "none",
  position: "absolute",
  top: "10px",
  left: "760px",
  width: "400px"
}} className="VideoItemsTwo">

    <section  className="RelatedVideo">
        <div className="VideoRelatedTitle">
            <h3>Related Items</h3>
        </div>
        <div className="VideoRelatedMain">
            <ul>
                {RelatedVideos2.map((video)=>{
return <li>
    <a href={`/${video.data.videoId}`}><img style={{
        borderRadius:"5px"
    }}src={video.data.downloadPhoto} width="103" height="73" alt="" />
    <h4>{video.data.title}</h4></a>
    </li>
                }).reverse()}
            </ul>
        </div>
    </section>
</div> */}
</div>
<div className="videoButtons"> 
  <button disabled={JSON.parse(localStorage.getItem('page'))[0].data.videoId==element.data.videoId} className="prevVideo" onClick={()=>{
    window.location.href=`/${Number(element.data.videoId)-1}`
  }}><span>{'<'} Prev</span></button>
 <button disabled={JSON.parse(localStorage.getItem('page'))[JSON.parse(localStorage.getItem('page')).length-1].data.videoId == element.data.videoId} className="nextVideo" onClick={()=>{
    window.location.href=`/${Number(element.data.videoId)+1}`
  }}><span>Next {'>'}</span></button>
</div>


<div className="previewInform2">
                                    <div className="previewInfOne2">
                                        <div className="previewLocation2">
                                            <div className="prevLoc2">Location</div>
                                            <div className="prevLocInf2">{element.data.city == '-' ? element.data.country :`${element.data.city}, ${element.data.country}`}</div>
                                        </div>
                                        <div className="previewLocation2">
                                            <div className="prevOcr2">Occurred</div>
                                            <div className="prevOcrInf2">{new Date(element.data.videoCreateDate).toLocaleDateString("en-US", { year: 'numeric', month: 'long', day: 'numeric'})}</div>
                                        </div>
                                    </div>
                                    <div className="previewInfTwo2">
                                    <div className="previewLocation2">
                                            <div className="prevOcr2">Video Id</div>
                                            <div className="prevPostedBy2">{element.data.videoId }</div>
                                        </div>
                                        <div className="previewLocation2">
                                            <div className="prevOcr2">Posted On</div>
                                            <div className="prevPostedBy2">{new Date(element.data.date).toLocaleDateString("en-US", { year: 'numeric', month: 'long', day: 'numeric'})}</div>
                                        </div>
                                    </div>
                                </div>
                                <section className="VideoTags">
                                    <h3>Tags</h3>
                                    <div className="VideoTagsList">
                                        <ul>
                                            {element.data.tags.replace(/\s/g, '').split(',').map((tag)=>{

                                                
                                                return <li><a href={`/library?${tag}`}>{tag}</a></li>
                                            })}
                                            
                                        </ul>
                                    </div>
                                </section>
                                <p style={{marginTop:"10px",fontWeight:"bold",display:'flex',justifyContent:"center"}}>Available for license. Email licensing@viralbear.media</p>
</div>
<div  style={{
  display: "flex"
}}className="VideoItemsTwo">

    <section  className="RelatedVideo">
        <div className="VideoRelatedTitle">
            <h3>Related Items</h3>
        </div>
        <div className="VideoRelatedMain">
            <ul>
                {RelatedVideos2.map((video)=>{
return <li>
    <a href={`/${video.data.videoId}`}><img style={{
        borderRadius:"5px"
    }}src={video.data.downloadPhoto} width="103" height="73" alt="" />
    <h4>{video.data.title}</h4></a>
    </li>
                }).reverse()}
            </ul>
        </div>
    </section>
</div>
       </div> 
       </div>
      </>
    );
}

export default withRouter(Post);